* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    width: 100%;
    font-size: 1.3rem;
    color: grey;
}

.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    display: inline-flex;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card img {
    max-height: 170px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}